body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.grow, .fade, .spin {
  -webkit-backface-visibility: none;
          backface-visibility: none;
  -webkit-perspective: 1000;
          perspective: 1000;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.grow:before, .fade:before, .spin:before {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

.grow:before {
  -webkit-animation-name: grow;
          animation-name: grow;
}

.fade:hover:before {
  -webkit-animation-name: fade;
          animation-name: fade;
}

.spin:hover:before {
  -webkit-animation-name: spin;
          animation-name: spin;
}

.status {
  display: inline-block;
  margin: 4px;
  width: 18px;
  height: 18px;
  border-radius: 9px;
  border: 2px solid transparent;
  box-sizing: border-box;
  position: relative;
  cursor: pointer;
}
.status:before {
  position: absolute;
  top: 50%;
  left: 50%;
}
.status.passed {
  border-color: #a3aebb;
}
.status.passed:before {
  content: '';
  border-bottom: 2px solid #a3aebb;
  width: 6px;
  margin-left: -3px;
  margin-top: -1px;
  display: block;
}
.status.pending {
  border-color: #FFF;
}
.status.pending:before {
  content: '';
  display: block;
  height: 6px;
  width: 2px;
  margin-top: -3px;
  margin-left: -3px;
  border-right: 2px solid #FFF;
  border-left: 2px solid #FFF;
}
.status.error {
  border-color: #ed9f03;
  border-width: 2px;
}
.status.error:before {
  content: '';
  margin-left: -5px;
  margin-top: -5px;
  width: 10px;
  height: 10px;
  background: #ed9f03;
  border-radius: 5px;
}
.status.stopped {
  border-color: #f04848;
}
.status.stopped:before {
  content: '';
  margin-left: -5px;
  margin-top: -5px;
  width: 10px;
  height: 10px;
  background: #f04848;
  border-radius: 5px;
}
.status.running {
  border-color: #3cb87e;
}
.status.running:before {
  content: '';
  margin-left: -5px;
  margin-top: -5px;
  width: 10px;
  height: 10px;
  background: #3cb87e;
  border-radius: 5px;
}
.status.waiting {
  border-color: #bbb;
}
.status.waiting:before {
  width: 3px;
  height: 4px;
  top: 2px;
  margin-left: -1px;
  content: '';
  border-left: 2px solid #bbb;
  border-bottom: 2px solid #bbb;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes fade {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

